import React from 'react'

const TheBestEcommerceIcon = (props) => {
  return (
    <svg
    width={59}
    height={57}
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    {...props}
  >
    <path fill="url(#a)" d="M0 0h59v57H0z" />
    <defs>
      <pattern
        id="a"
        patternContentUnits="objectBoundingBox"
        width={1}
        height={1}
      >
        <use xlinkHref="#b" transform="matrix(.00072 0 0 .00072 -.004 0)" />
      </pattern>
      <image
        id="b"
        width={1390}
        height={1333}
        xlinkHref="data:image/png;base64,"
      />
    </defs>
  </svg>
  )
}

export default TheBestEcommerceIcon